/**
 * ResultsDisplay Component
 * Displays the results of the tip calculation, including each partner's
 * tip amount and bill breakdown
 */

import React from 'react';
import styles from '../styles/ResultsDisplay.module.css';
import { Partner } from '../types';

interface ResultsDisplayProps {
  partners: Partner[];
  remainderCents: number;
}

const ResultsDisplay: React.FC<ResultsDisplayProps> = ({ partners, remainderCents }) => {
  // Check if any partner has a tip amount (to determine if calculation was done)
  const hasTipAmounts = partners.some(partner => partner.tipAmount !== undefined);

  if (!hasTipAmounts) {
    return null; // Don't render anything if no calculations were done
  }

  // Calculate total tip amount distributed
  const totalDistributed = partners.reduce(
    (sum, partner) => sum + (partner.tipAmount || 0),
    0
  );

  return (
    <div className={styles.resultsContainer}>
      <h2>Tip Distribution Results</h2>
      
      <div className={styles.summaryBox}>
        <div className={styles.summaryItem}>
          <span className={styles.summaryLabel}>Total Tips Distributed:</span>
          <span className={styles.summaryValue}>${totalDistributed.toFixed(2)}</span>
        </div>
        
        <div className={styles.summaryItem}>
          <span className={styles.summaryLabel}>Remainder Cents:</span>
          <span className={styles.summaryValue}>${(remainderCents / 100).toFixed(2)}</span>
        </div>
      </div>
      
      <div className={styles.tableContainer}>
        <table className={styles.resultsTable}>
          <thead>
            <tr>
              <th>Partner Name</th>
              <th>Partner Number</th>
              <th>Hours</th>
              <th>Tip Amount</th>
              <th>Bill Breakdown</th>
            </tr>
          </thead>
          <tbody>
            {partners.map((partner, index) => (
              <tr key={index}>
                <td>{partner.name}</td>
                <td>{partner.number}</td>
                <td>{partner.hours.toFixed(2)}</td>
                <td>${partner.tipAmount?.toFixed(2) || '0.00'}</td>
                <td>
                  {partner.billBreakdown ? (
                    <div className={styles.billBreakdown}>
                      {partner.billBreakdown.twenties > 0 && (
                        <span className={styles.bill}>
                          {partner.billBreakdown.twenties} × $20
                        </span>
                      )}
                      {partner.billBreakdown.tens > 0 && (
                        <span className={styles.bill}>
                          {partner.billBreakdown.tens} × $10
                        </span>
                      )}
                      {partner.billBreakdown.fives > 0 && (
                        <span className={styles.bill}>
                          {partner.billBreakdown.fives} × $5
                        </span>
                      )}
                      {partner.billBreakdown.ones > 0 && (
                        <span className={styles.bill}>
                          {partner.billBreakdown.ones} × $1
                        </span>
                      )}
                    </div>
                  ) : (
                    'N/A'
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      
      <div className={styles.printSection}>
        <button 
          onClick={() => window.print()}
          className={styles.printButton}
        >
          Print Results
        </button>
      </div>
    </div>
  );
};

export default ResultsDisplay; 