import React, { useState } from 'react';
import './App.css';
import FileUpload from './components/FileUpload';
import ManualInput from './components/ManualInput';
import TipCalculator from './components/TipCalculator';
import ResultsDisplay from './components/ResultsDisplay';
import { Partner, AppState } from './types';

/**
 * Main App Component for the Starbucks Tips Calculator
 */
const App: React.FC = () => {
  // Application state
  const [state, setState] = useState<AppState>({
    partners: [],
    totalTipAmount: 0,
    remainderCents: 0,
    ocrInProgress: false,
    ocrError: null
  });

  /**
   * Handle when OCR processing starts
   */
  const handleOcrStart = () => {
    setState(prevState => ({
      ...prevState,
      ocrInProgress: true,
      ocrError: null
    }));
  };

  /**
   * Handle OCR processing completion
   */
  const handleOcrComplete = (partners: Partner[]) => {
    setState(prevState => ({
      ...prevState,
      partners,
      ocrInProgress: false
    }));
  };

  /**
   * Handle OCR processing errors
   */
  const handleOcrError = (error: string) => {
    setState(prevState => ({
      ...prevState,
      ocrInProgress: false,
      ocrError: error
    }));
  };

  /**
   * Handle partner data changes from the manual input
   */
  const handlePartnersChange = (partners: Partner[]) => {
    setState(prevState => ({
      ...prevState,
      partners
    }));
  };

  /**
   * Handle tip calculation completion
   */
  const handleCalculationComplete = (partners: Partner[], remainderCents: number) => {
    setState(prevState => ({
      ...prevState,
      partners,
      remainderCents
    }));
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>Starbucks Tips Calculator</h1>
        <p className="App-subtitle">Equitably distribute tips among partners</p>
      </header>

      <main className="App-main">
        {/* File Upload Section */}
        <section className="App-section">
          <FileUpload
            onOcrStart={handleOcrStart}
            onOcrComplete={handleOcrComplete}
            onOcrError={handleOcrError}
          />
          
          {state.ocrInProgress && (
            <div className="App-loading">
              <p>Processing file, please wait...</p>
            </div>
          )}
          
          {state.ocrError && (
            <div className="App-error">
              <p>Error: {state.ocrError}</p>
              <p>Please try again or use manual input below.</p>
            </div>
          )}
        </section>

        {/* Manual Input Section */}
        <section className="App-section">
          <ManualInput
            partners={state.partners}
            onPartnersChange={handlePartnersChange}
          />
        </section>

        {/* Tip Calculator Section */}
        <section className="App-section">
          <TipCalculator
            partners={state.partners}
            remainderCents={state.remainderCents}
            onCalculationComplete={handleCalculationComplete}
          />
        </section>

        {/* Results Display Section */}
        <section className="App-section">
          <ResultsDisplay
            partners={state.partners}
            remainderCents={state.remainderCents}
          />
        </section>
      </main>

      <footer className="App-footer">
        <p>© {new Date().getFullYear()} Starbucks Tips Calculator</p>
        <p className="App-footer-note">
          This app is not affiliated with or endorsed by Starbucks Corporation.
        </p>
      </footer>
    </div>
  );
};

export default App;
