/**
 * ManualInput Component
 * Allows users to manually input partner data, with pre-populated fields if OCR data is available
 */

import React, { useState, useEffect } from 'react';
import styles from '../styles/ManualInput.module.css';
import { Partner } from '../types';

interface ManualInputProps {
  partners: Partner[];
  onPartnersChange: (partners: Partner[]) => void;
}

const ManualInput: React.FC<ManualInputProps> = ({ partners, onPartnersChange }) => {
  // Create a working copy of partners that can be edited
  const [editablePartners, setEditablePartners] = useState<Partner[]>(partners);

  // Update editable partners when the prop changes (e.g., after OCR)
  useEffect(() => {
    setEditablePartners(partners);
  }, [partners]);

  /**
   * Handle input change for a partner field
   */
  const handlePartnerChange = (index: number, field: keyof Partner, value: string | number) => {
    const updatedPartners = [...editablePartners];
    
    // Handle number inputs
    if (field === 'hours') {
      // Ensure hours is a valid number
      const hoursValue = typeof value === 'string' ? parseFloat(value) : value;
      updatedPartners[index] = {
        ...updatedPartners[index],
        [field]: isNaN(hoursValue) ? 0 : hoursValue
      };
    } else {
      // Handle string inputs
      updatedPartners[index] = {
        ...updatedPartners[index],
        [field]: value
      };
    }
    
    setEditablePartners(updatedPartners);
    onPartnersChange(updatedPartners);
  };

  /**
   * Add a new empty partner row
   */
  const addPartner = () => {
    const newPartner: Partner = {
      name: '',
      number: '',
      hours: 0
    };
    
    const updatedPartners = [...editablePartners, newPartner];
    setEditablePartners(updatedPartners);
    onPartnersChange(updatedPartners);
  };

  /**
   * Remove a partner row
   */
  const removePartner = (index: number) => {
    const updatedPartners = editablePartners.filter((_, i) => i !== index);
    setEditablePartners(updatedPartners);
    onPartnersChange(updatedPartners);
  };

  return (
    <div className={styles.manualInputContainer}>
      <h2>Partner Data</h2>
      <p>Edit partner data below or add new partners</p>
      
      <div className={styles.tableContainer}>
        <table className={styles.partnersTable}>
          <thead>
            <tr>
              <th>Partner Name</th>
              <th>Partner Number</th>
              <th>Total Tippable Hours</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {editablePartners.map((partner, index) => (
              <tr key={index}>
                <td>
                  <input
                    type="text"
                    value={partner.name}
                    onChange={(e) => handlePartnerChange(index, 'name', e.target.value)}
                    placeholder="Partner Name"
                    className={styles.inputField}
                  />
                </td>
                <td>
                  <input
                    type="text"
                    value={partner.number}
                    onChange={(e) => handlePartnerChange(index, 'number', e.target.value)}
                    placeholder="Partner Number"
                    className={styles.inputField}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={partner.hours}
                    onChange={(e) => handlePartnerChange(index, 'hours', e.target.value)}
                    placeholder="Hours"
                    step="0.01"
                    min="0"
                    className={styles.inputField}
                  />
                </td>
                <td>
                  <button 
                    onClick={() => removePartner(index)}
                    className={styles.removeButton}
                    aria-label="Remove partner"
                  >
                    ✕
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      
      <button 
        onClick={addPartner}
        className={styles.addButton}
      >
        + Add Partner
      </button>
    </div>
  );
};

export default ManualInput; 