/**
 * OCR Service using Azure Computer Vision API
 * Provides functionality to extract text from images and PDFs
 */

import { ComputerVisionClient, ComputerVisionModels } from '@azure/cognitiveservices-computervision';
import { ApiKeyCredentials } from '@azure/ms-rest-js';
import { Partner } from '../types';

// Azure Computer Vision API credentials
const azureKey = '6OaBlmEj6cevfq2dPdCdWIVfrG0mVNBQ4QYvJIBMTWr7cwhN7l5wJQQJ99BCACYeBjFXJ3w3AAAFACOG73Ks';
const azureEndpoint = 'https://walshyy.cognitiveservices.azure.com/';

/**
 * Extract text using Azure Computer Vision API
 * @param file The image or PDF file to extract text from
 * @returns A promise that resolves to the extracted text
 */
export const extractTextWithAzure = async (file: File): Promise<string> => {
  try {
    // Create Computer Vision client
    const credentials = new ApiKeyCredentials({ 
      inHeader: { 'Ocp-Apim-Subscription-Key': azureKey } 
    });
    const client = new ComputerVisionClient(credentials, azureEndpoint);
    
    // Convert file to ArrayBuffer and then to Buffer/Uint8Array
    const arrayBuffer = await file.arrayBuffer();
    const buffer = new Uint8Array(arrayBuffer);
    
    // Read text from the image/document
    const options: ComputerVisionModels.ComputerVisionClientReadInStreamOptionalParams = {
      language: 'en',
      rawHttpResponse: false
    };
    
    // Start the read operation
    const result = await client.readInStream(buffer, options);
    
    // Get operation ID from operation-location header
    if (!result.operationLocation) {
      throw new Error('Operation location not found in response');
    }
    
    // Extract the operation ID from the operation location URL
    const operationIdMatch = result.operationLocation.match(/\/operations\/([^/]+)$/);
    if (!operationIdMatch || operationIdMatch.length < 2) {
      throw new Error('Operation ID not found in response');
    }
    const operationId = operationIdMatch[1];
    
    // Poll until the operation completes
    let extractedText = '';
    let isOperationSuccessful = false;
    let retries = 10; // Limit the number of polling attempts
    
    while (!isOperationSuccessful && retries > 0) {
      const readResult = await client.getReadResult(operationId);
      
      if (readResult.status === 'succeeded') {
        isOperationSuccessful = true;
        
        // Combine all extracted text
        if (readResult.analyzeResult && readResult.analyzeResult.readResults) {
          for (const page of readResult.analyzeResult.readResults) {
            for (const line of page.lines || []) {
              extractedText += line.text + '\n';
            }
          }
        }
      } else if (readResult.status === 'failed') {
        throw new Error('Text extraction operation failed');
      } else {
        // Wait before polling again
        await new Promise(resolve => setTimeout(resolve, 1000));
        retries--;
      }
    }
    
    if (!isOperationSuccessful) {
      throw new Error('Text extraction operation did not complete in time');
    }
    
    if (!extractedText) {
      throw new Error('No content extracted');
    }
    
    return extractedText;
  } catch (error) {
    console.error('Error extracting text with Azure Computer Vision:', error);
    throw new Error('Text extraction with Azure Computer Vision failed');
  }
};

/**
 * Parse extracted text to find partner data
 * @param text The extracted text from an OCR service
 * @returns An array of partner objects
 */
export const parsePartnerData = (text: string): Partner[] => {
  try {
    const partners: Partner[] = [];
    
    // Regular expressions to find the headings and data
    const headingPattern = /(Partner Name|Partner Number|Total Tippable Hours)/gi;
    const dataPattern = /([A-Za-z\s]+)\s+(\d+)\s+(\d+(\.\d+)?)/g;
    
    // Check if the headings exist in the text
    if (!headingPattern.test(text)) {
      throw new Error('Required headings not found in the document');
    }
    
    // Extract partner data
    let match;
    while ((match = dataPattern.exec(text)) !== null) {
      // Extracted data: name, number, hours
      const name = match[1].trim();
      const number = match[2].trim();
      const hours = parseFloat(match[3]);
      
      // Only add if we have valid data
      if (name && number && !isNaN(hours)) {
        partners.push({ name, number, hours });
      }
    }
    
    return partners;
  } catch (error) {
    console.error('Error parsing partner data:', error);
    throw new Error('Failed to parse partner data from extracted text');
  }
};

/**
 * Extract partner data from a file using OCR
 * @param file The image or PDF file to extract data from
 * @returns A promise that resolves to an array of partner objects
 */
export const extractPartnerData = async (file: File): Promise<Partner[]> => {
  try {
    // Extract text using Azure Computer Vision API
    const text = await extractTextWithAzure(file);
    
    // Parse the extracted text to find partner data
    return parsePartnerData(text);
  } catch (error) {
    console.error('Error extracting partner data:', error);
    throw error;
  }
}; 