/**
 * Utility functions for tip calculation and bill allocation
 */

import { Partner, BillBreakdown } from '../types';

/**
 * Calculate the tip amount for each partner based on hours worked
 * @param partners Array of partners with hours worked
 * @param totalTipAmount Total tip amount to distribute
 * @param previousRemainderCents Cents remaining from previous calculations
 * @returns Object containing updated partners array with tip amounts and new remainder cents
 */
export const calculateTips = (
  partners: Partner[],
  totalTipAmount: number,
  previousRemainderCents: number = 0
): { partners: Partner[], remainderCents: number } => {
  // Calculate total hours worked by all partners
  const totalHours = partners.reduce((sum, partner) => sum + partner.hours, 0);
  
  // Add previous remainder cents to the total tip amount
  const totalAmountInCents = Math.floor(totalTipAmount * 100) + previousRemainderCents;
  
  // Updated partners array with calculated tips
  const updatedPartners = partners.map(partner => {
    // Calculate the partner's share of tips in cents
    const proportion = partner.hours / totalHours;
    const tipAmountInCents = Math.floor(proportion * totalAmountInCents);
    
    // Convert cents to dollars (rounded down to nearest dollar)
    const tipAmount = Math.floor(tipAmountInCents / 100);
    
    return {
      ...partner,
      tipAmount
    };
  });
  
  // Calculate the sum of all distributed tips in cents
  const distributedTipsInCents = updatedPartners.reduce(
    (sum, partner) => sum + (partner.tipAmount || 0) * 100, 
    0
  );
  
  // Calculate remainder cents
  const remainderCents = totalAmountInCents - distributedTipsInCents;
  
  return { partners: updatedPartners, remainderCents };
};

/**
 * Allocate bills for a tip amount using $20, $10, $5, and $1 bills
 * @param tipAmount Tip amount in dollars
 * @returns Bill breakdown object
 */
export const allocateBills = (tipAmount: number): BillBreakdown => {
  // Initialize bill counts
  let twenties = 0;
  let tens = 0;
  let fives = 0;
  let ones = 0;
  
  // Track remaining amount
  let remainingAmount = tipAmount;
  
  // Allocate $20 bills
  twenties = Math.floor(remainingAmount / 20);
  remainingAmount -= twenties * 20;
  
  // Allocate $10 bills
  tens = Math.floor(remainingAmount / 10);
  remainingAmount -= tens * 10;
  
  // Allocate $5 bills
  fives = Math.floor(remainingAmount / 5);
  remainingAmount -= fives * 5;
  
  // Allocate $1 bills
  ones = remainingAmount;
  
  return { twenties, tens, fives, ones };
};

/**
 * Calculate tips and allocate bills for each partner
 * @param partners Array of partners with hours worked
 * @param totalTipAmount Total tip amount to distribute
 * @param previousRemainderCents Cents remaining from previous calculations
 * @returns Object containing updated partners array with tip amounts and bill allocations, and new remainder cents
 */
export const calculateTipsWithBillAllocation = (
  partners: Partner[],
  totalTipAmount: number,
  previousRemainderCents: number = 0
): { partners: Partner[], remainderCents: number } => {
  // Calculate tips
  const { partners: partnersWithTips, remainderCents } = calculateTips(
    partners,
    totalTipAmount,
    previousRemainderCents
  );
  
  // Allocate bills for each partner
  const partnersWithBillBreakdown = partnersWithTips.map(partner => {
    if (partner.tipAmount === undefined || partner.tipAmount === 0) {
      return partner;
    }
    
    const billBreakdown = allocateBills(partner.tipAmount);
    
    return {
      ...partner,
      billBreakdown
    };
  });
  
  return { partners: partnersWithBillBreakdown, remainderCents };
}; 