/**
 * TipCalculator Component
 * Allows users to enter the total tip amount and calculate tips for each partner
 */

import React, { useState } from 'react';
import styles from '../styles/TipCalculator.module.css';
import { Partner } from '../types';
import { calculateTipsWithBillAllocation } from '../utils/calculationUtils';

interface TipCalculatorProps {
  partners: Partner[];
  remainderCents: number;
  onCalculationComplete: (partners: Partner[], remainderCents: number) => void;
}

const TipCalculator: React.FC<TipCalculatorProps> = ({
  partners,
  remainderCents,
  onCalculationComplete
}) => {
  const [tipAmount, setTipAmount] = useState<string>('');
  const [error, setError] = useState<string | null>(null);

  /**
   * Handle tip amount input change
   */
  const handleTipAmountChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setTipAmount(e.target.value);
    setError(null);
  };

  /**
   * Calculate tips for all partners
   */
  const calculateTips = () => {
    // Validate input
    if (!tipAmount || isNaN(parseFloat(tipAmount))) {
      setError('Please enter a valid tip amount');
      return;
    }

    if (partners.length === 0) {
      setError('Please add at least one partner');
      return;
    }

    // Check if all partners have hours
    const invalidPartners = partners.some(partner => !partner.hours || partner.hours <= 0);
    if (invalidPartners) {
      setError('All partners must have tippable hours greater than 0');
      return;
    }

    // Calculate tips
    const totalTipAmount = parseFloat(tipAmount);
    const { partners: updatedPartners, remainderCents: newRemainderCents } = 
      calculateTipsWithBillAllocation(partners, totalTipAmount, remainderCents);
    
    // Send results back to parent component
    onCalculationComplete(updatedPartners, newRemainderCents);
  };

  /**
   * Calculate total hours for all partners
   */
  const totalHours = partners.reduce((sum, partner) => sum + partner.hours, 0);

  return (
    <div className={styles.tipCalculatorContainer}>
      <h2>Calculate Tips</h2>
      
      <div className={styles.calculatorForm}>
        {totalHours > 0 && (
          <div className={styles.totalHoursDisplay}>
            Total Hours: <span>{totalHours.toFixed(2)}</span>
          </div>
        )}
        
        {remainderCents > 0 && (
          <div className={styles.remainderCentsDisplay}>
            Remainder from previous calculation: <span>${(remainderCents / 100).toFixed(2)}</span>
          </div>
        )}
        
        <div className={styles.inputGroup}>
          <label htmlFor="tipAmount">Total Tip Amount ($):</label>
          <input
            type="number"
            id="tipAmount"
            value={tipAmount}
            onChange={handleTipAmountChange}
            className={styles.tipAmountInput}
            placeholder="0.00"
            step="0.01"
            min="0"
          />
        </div>
        
        {error && <div className={styles.errorMessage}>{error}</div>}
        
        <button 
          onClick={calculateTips}
          className={styles.calculateButton}
          disabled={partners.length === 0}
        >
          Calculate Tips
        </button>
      </div>
    </div>
  );
};

export default TipCalculator; 